<template>
    <p-progress-spinner />
</template>

<script>
import { onMounted } from "vue-demi";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex";
import { clearPaitentAndCycle } from "@/service/utils";

export default {
    setup() {
        const route = useRoute();
        const router = useRouter();
        const store = useStore();

        onMounted(() => {
            const { patientId, to, providerNo } = route.query;

            if (patientId == "0") {
                clearPaitentAndCycle();
                store.commit("artCycle/CLEAR_PATIENT_AND_CYCLE_DATA");
                router.push({ path: "art-cycle" });
                return;
            } else {
                store
                    .dispatch("artCycle/getCyclesDetails", patientId)
                    .then(() => {
                        router.push({ path: to });
                    })
                    .catch((err) => {});
            }

            // if (store.state.user.providerNo === providerNo) {
            // } else {
            //     store.dispatch("logout").then(() => {
            //         router.push({
            //             name: "Login",
            //             query: { to, providerNo, patientId }
            //         });
            //     });
            // }
        });
    }
};
</script>
